import React from 'react';
import P5 from "p5";

export const BackgroundSketch = () => {
    
    new P5(Sketch)
    
    return (
        <></>
    )
}

const Sketch = (p5 : P5) => {

    let img : P5.Image

    p5.preload = () => {
        img = p5.loadImage('https://i.imgur.com/QDk5aRz.png');
    }

    p5.setup = () => {
        const canvas = p5.createCanvas(p5.displayWidth, p5.displayHeight)
        canvas.parent("app")
        p5.frameRate(1)
    }

    // The sketch draw method
    p5.draw = () => {
        console.log(p5.random(p5.displayWidth) + " " + p5.random(p5.displayHeight))
        p5.image(img, p5.random(p5.displayWidth-55), p5.random(p5.displayHeight-120), 55, 120)
        
    }
}